<script>
import dutyfreeBestsellerslider from "../../components/dutyfreeBestsellerslider";
import dutyfreeNewarrival from "../../components/dutyfreeNewarrival";
import rightStickIcon from "../../components/rightStickIcon";
import axios from "axios";
export default {
  components: {
    dutyfreeBestsellerslider,
    dutyfreeNewarrival,
    rightStickIcon,
  },

  data() {
    return {
      BestsellersSlider: "",
      NewArrivalSlider: "",

      slider: [
        "red",
        "green",
        "orange",
        "blue",
        "pink",
        "purple",
        "indigo",
        "cyan",
        "deep-purple",
        "light-green",
        "deep-orange",
        "blue-grey",
      ],

      items: [
        {
          src: require("../../views/attheairport/images/dutyfree-banner.jpg"),
          title: "Discover Premium Brands at Irresistible Prices Before You Fly!",
        },
        {
          src: require("../../views/attheairport/images/dutyfree-banner-2.jpg"),
          title: "Indulge in a Tax-Free Shopping Extravaganza at the Duty-Free Shops",
        },
      ],
      materialGroup: [],
    };
  },
  mounted() {
    this.getMeterialGroup();
  },
  methods: {
    getColor(status) {
      if (status == "Delayed") return "red";
      else return "green";
    },
    async getMeterialGroup(data = {}) {
      let materialGroup = await axios.post(
        `${process.env.VUE_APP_AIRLlINE_URL}/user/material-group`,
        {
          page: 1,
          limit: 5,
          sortKey: "name",
          sortDirection: "ASC",
          search: "",
        }
      );
      console.log("materialGroup", materialGroup.data.success);
      let materialData = [];
      if (materialGroup.data.success)
        materialData = materialGroup.data.data.docs;

      //   console.log("this.materialGroup ", this.materialGroup);

      for (let material of materialData) {
        let product = await axios.post(
          `${process.env.VUE_APP_AIRLlINE_URL}/user/product`,
          {
            page: 1,
            limit: data._id != undefined && data._id == material._id ? 100 : 3,
            search: "",
            sortKey: "name",
            sortDirection: "ASC",
            filters: {
              materialGroup:
                data._id != undefined && data._id == material._id
                  ? data._id
                  : material._id,
            },
          }
        );
        // console.log("product ", product);
        material["product"] = [];
        if (product.data.success) material["product"] = product.data.data.docs;
      }
      this.materialGroup = materialData;
      console.log("  this.materialGroup :: 450", this.materialGroup);
    },
    viewall(data) {
      console.log("view All", data);
    },
  },

  computed: {
    columns() {
      if (this.$vuetify.breakpoint.xl) {
        return 4;
      }

      if (this.$vuetify.breakpoint.lg) {
        return 3;
      }

      if (this.$vuetify.breakpoint.md) {
        return 2;
      }

      return 1;
    },
  },
};
</script>
