<template>
  <v-carousel
    :show-arrows="false"
    class="dutyfreecarousel"
    :class="{ charmSmall: $vuetify.breakpoint.smAndDown }"
    height="380px"
  >
    <template v-for="(item, index) in slider">
      <v-carousel-item
        v-if="(index + 1) % columns === 1 || columns === 1"
        :key="index"
      >
        <v-row class="flex-nowrap">
          <template v-for="(prd, index) of product" >
            <!-- <template v-if="+index + i < slider.length"> -->
            <v-col :key="index">
              <v-sheet
              
                height="180px"
                width="360px"
                class="px-1 bg-secondary rounded-xl mt-10"
              >
                <v-row
                  class="fill-height px-1 bg-secondary rounded-xl"
                 
                >
                <v-col justify="center"  align="center">
                  <v-hover>
                    <div
                      class="display-3 px-1 bg-secondary rounded-xl"
                      :class="`${hover ? 'imgover' : 'imgleave'}`"
                      slot-scope="{ hover }"
                    >
                      <v-img :src="url + prd.images[0]" width="300px"></v-img>
                    </div>
                  </v-hover>
                </v-col>
                </v-row>
                <v-row class="fill-height">
                  <v-col justify="center" align="center">
                    <p>{{ prd.name }}</p>
                  </v-col>
                  <!-- <v-col cols="4" md="4">
                    <div class="amtBox ml-5">₹ 7,250</div>
                  </v-col> -->
                </v-row>
              </v-sheet>
            </v-col>
            <!-- </template> -->
          </template>
        </v-row>
      </v-carousel-item>
    </template>
  </v-carousel>
</template>
<script>
export default {
  name: "dutyfreeBestsellerslider",
  props: ["product"],
  data() {
    return {
      slider: [
        "light-bluecolor",
        "light-purple",
        "light-yellow",
        "light-turquoise",
        "light-lemon",
        "skyblue",
        "light-peach",
        "light-pink",
        "lightskyblue",
      ],
      url: process.env.VUE_APP_AIRLlINE_URL,
    };
  },
  mounted() {
    console.log("FRom child", this.product);
  },
  computed: {
    columns() {
      if (this.$vuetify.breakpoint.xl) {
        return 4;
      }

      if (this.$vuetify.breakpoint.lg) {
        return 3;
      }

      if (this.$vuetify.breakpoint.md) {
        return 2;
      }

      return 1;
    },
  },
};
</script>
<style scoped>
.light-purple {
  background-color: rgba(226, 214, 255, 0.4);
}

.light-bluecolor {
  background-color: rgba(171, 222, 230, 0.4);
}

.light-yellow {
  background-color: rgba(239, 221, 194, 0.7);
}

.light-lemon {
  background-color: rgba(255, 246, 216, 0.7);
}

.light-peach {
  background-color: rgba(241, 223, 205, 0.4);
}

.light-pink {
  background-color: rgba(235, 201, 195, 0.4);
}

.light-turquoise {
  background-color: rgba(101, 140, 173, 0.7);
}

.skyblue {
  background-color: rgba(171, 201, 230, 0.4);
}

.lightskyblue {
  background-color: rgba(209, 213, 219, 0.6);
}

.dutyfreecarousel p {
  font-family: "DM Sans Regular";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 28px;
  letter-spacing: -0.4px;
  color: #374151;
  text-align: center;
 
}

.amtBox {
  width: 80px;
  height: 40px;
  padding-top: 10px;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  background: #e8effe;
  border-radius: 100px;
  color: #1b63f8;
  text-align: center;
}

.charmSmall.dutyfreecarousel {
  width: 100%;
}
.charmSmall.dutyfreecarousel p {
  font-size: 16px !important;
}
.charmSmall.amtBox {
  font-size: 12px !important;
}
</style>